.header {
  position: absolute;
  z-index: 3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 2em;
  @media (min-width : $bp-tablet) {
    display: grid;
    grid-template-columns: repeat(24, 1fr);
    grid-gap: 16px;
    grid-column-gap: 16px;
    justify-content: center;
  }

  &__volum {
    @media (min-width : $bp-tablet) {
      grid-column: 1 / span 3;
    }
    justify-self: left;

    & .link__label {
      font-family: $tipo--secundary;
      font-size: 15px;
      color: $color--base;
      letter-spacing: 0.62px;
      text-transform: uppercase;
    }

    .link {
      z-index: 4;
    }
  }

  &__logo {
    @media (min-width : $bp-tablet) {
      grid-column: 4 / span 18;
    }
    // justify-self: center;
    text-align: center;

    & h4 {
      font-weight: normal;
      font-family: "gt_cinetypebold", sans-serif;
      font-size: 11px;
      color: $color--base;
      letter-spacing: 0;
      text-align: center;
      text-transform: uppercase;
      display: block;
      margin-bottom: .5em;
      @media (min-width : 768px) {
        margin-bottom: 1em;
        font-size: 13px;
      }
    }

    & img, & svg {
      max-width: 224px;
      fill: $color--base;
      @media (max-width : 575px) {
        max-width:129px;
      }
    }
  }

  &__locale-selector {
    @media (min-width : $bp-tablet) {
      grid-column: -4 / span 3;
    }
    justify-self: right;

    .link {
      z-index: 4;
    }
  }
}


// Menú Volum
.menu-volum {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: $bgcolor--fosc;
  z-index: 3;
  padding: 120px 20px 20px;
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  overflow: scroll;
  display: none;
  @media (min-width : $bp-desktop) {
    width: 43vw;
    padding: 120px 60px;
  }
  @media (min-width : $bp-desktop-xl) {
    width: 33vw;
  }

  & li a {
    display: flex;
    flex-flow: row;
    margin-bottom: 64px;
    opacity: 0.4;

    &:hover {
      opacity: 1;
    }

    &.select {
      opacity: 1;
    }
  }

  &__img {
    max-width: 106px;
    margin-right: 16px;
  }

  &__text {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-end;
    align-items: flex-start;
  }

  &__numero {
    font-family: $tipo--primary;
    font-weight: $bold--primary;
    font-size: 18px;
    color: $color--white;
    letter-spacing: 0;
    text-align: center;
    border-bottom: 3px solid $color--white;
    padding-bottom: .25em;
    margin-bottom: 1em;
    margin-left: 1em;
    @media (min-width : $bp-tablet) {
      font-size: 20px;
      margin-left: 1.5em;
    }
  }

  &__titol {
    font-family: $tipo--primary;
    font-weight: $bold--primary;
    font-size: 24px;
    color: $color--white;
    letter-spacing: -0.4px;
    @media (min-width : $bp-tablet) {
      font-size: 28px;
    }
  }
}

// Menú Idiome
.menu__locale-selector {
  position: fixed;
  top: 0;
  right: 0;
  width: 33vw;
  height: 100vh;
  background-color: $bgcolor--fosc;
  z-index: 3;
  padding: 120px 20px 20px;
  flex-flow: column;
  justify-content: flex-start;
  overflow: scroll;
  display: none;

  &.status-open {
    display: flex;
  }

  @media (min-width : $bp-desktop) {
    width: 43vw;
    padding: 120px 60px;
  }
  @media (min-width : $bp-desktop-xl) {
    width: 33vw;
  }

  & li a {
    font-family: $tipo--primary;
    font-weight: $bold--primary;
    font-size: 24px;
    color: $color--white;
    letter-spacing: -0.4px;
    display: flex;
    flex-flow: row;
    margin-bottom: 24px;
    opacity: 0.4;
    @media (min-width : $bp-tablet) {
      font-size: 28px;
    }

    &:hover {
      opacity: 1;
    }

    &.active {
      opacity: 1;
    }
  }
}

// Navegació vertical
.nav-vertical {
  // display: none;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 16px;
  // mix-blend-mode: difference;
  @media (min-width : $bp-tablet) {
    left: 64px;
  }

  & ul {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    transform: rotate(-90deg);
    transform-origin: left top;
    position: absolute;
    top: calc(100vh - (100vh - 491px)/2);
    width: 491px;
    @media (min-width : $bp-tablet) {
      top: calc(100vh - (100vh - 516px)/2);
      width: 516px;
    }
  }

  & li {
    margin: auto 8px;
    @media (min-width : $bp-tablet) {
      margin: auto 12px;
    }
  }

  & a {
    font-family: $tipo--secundary;
    font-size: 12px;
    color: $color--base;
    letter-spacing: 0.62px;
    text-transform: uppercase;
    padding-bottom: 6px !important;

    @media (min-width : $bp-tablet) {
      font-size: 15px;
    }

    &:hover, &.select {
      background: transparent linear-gradient(90deg , $color--primary 50%, $color--primary 0) no-repeat;
      background-size: 200% 2px;
      background-position: 100% 89%;
      color: $color--primary;
    }
  }
}
