
// Burguer

.burguer {
  position: relative;
  background-color: transparent;
  border: none;
  display: flex;
  flex-flow: row;
  align-items: center;
  z-index: 4;
  cursor: pointer;

  &:hover {
    text-decoration: underline;
  }

  &__label {
    text-decoration: none;
    text-transform: uppercase;
    font-family: $tipo--secundary;
    font-size: 1em;
    color: $color--base;
    letter-spacing: 0;
    position: relative;
    @media (max-width : 575px) {
      display: none;
    }
  }

  &__icon-close,
  &__icon-open {
    display: flex;
    width: 24px;
    height: 24px;
    align-items: center;
    justify-content: center;
    fill: $color--base;

    &--left {
      margin-right: 8px;
    }

    &--right {
      margin-left: 8px;
    }
  }

  .burguer__icon-open {
    width: 20px;
    width: 20px;
    display: block;
  }

  .burguer__icon-close {
    display: none;
  }

  &.status-open .burguer__icon-open {
    display: none;
  }

  &.status-open .burguer__icon-close {
    display: block;
  }
}
